<template>

    <div id="menu-bar">
        
      <img src="../assets/neres_poker_logo.png" width="150">
      <div></div>
    </div>

    <div class="flex-center flex-column" id="main-conatiner">

        <div id="voting-text-info">{{ tatalVoted }} de {{tatalPalyer}} votos</div>

        <div class="flex-center">

            <div v-for="pCard in playersCards" class="card-player flex-center flex-column">
                <div :class="'flex-center pick-card  ' + getCardCssClassByStep(pCard['step'])"> {{ isRevealed? pCard["vote"] : "" }}</div>
                {{ pCard["name"] }}
            </div>

        </div>
        
        <button v-if="!isRevealed" class="poker-button poker-button-primary" @click="onClickCardReveal">Revelar Cartas</button>
        <button v-if="isRevealed" class="poker-button poker-button-secundary" @click="onClickRestartGame">Nova Votao</button>

    </div>

    <div id="choose-card-conatiner">
        <div  v-if="!isRevealed" class="flex-center choose-card-height" >
            <div class="flex-center choose-card" @click="onClickChooseCard('0', 'choose-card-0')" id="choose-card-0">0</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('1', 'choose-card-1')" id="choose-card-1">1</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('2', 'choose-card-2')" id="choose-card-2">2</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('3', 'choose-card-3')" id="choose-card-3">3</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('5', 'choose-card-5')" id="choose-card-5">5</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('8', 'choose-card-8')" id="choose-card-8">8</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('13', 'choose-card-13')" id="choose-card-13">13</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('21', 'choose-card-21')" id="choose-card-21">21</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('34', 'choose-card-34')" id="choose-card-34">34</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('55', 'choose-card-55')" id="choose-card-55">55</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('89', 'choose-card-89')" id="choose-card-89">89</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('?', 'choose-card-interrogation')" id="choose-card-interrogation">?</div>
            <div class="flex-center choose-card" @click="onClickChooseCard('', 'choose-card-coffee')" id="choose-card-coffee"></div>
        </div>

        <div v-if="isRevealed" class="flex-center choose-card-height">

            <div v-for="vCard in votedCardsInfo" class="vote-card-info flex-center flex-column">
                <div class="flex-center voted-card">{{ vCard["number"] }}</div>
                {{ vCard["quantityLabel"] }}
            </div>

            <div class="vote-card-info flex-center flex-column" style="flex-direction: column-reverse;margin-left: 50px;">
                <div class="flex-center voted-card" style="width: 150px;">{{ this.average }}</div>
                Mdia
            </div>
        
        </div>

    </div>
</template>

<script>
    import { io } from "socket.io-client";

    export default {

        data() {
            return {
                namePalyer: "",
                idPalyer: "",
                socketInstance: null,
                playersCards: [],
                isRevealed: false,
                average: 0,
                votedCardsInfo: [],
                tatalVoted: 0,
                tatalPalyer: 0
            }
        },

        created() {
            if(sessionStorage.getItem(this.$route.params.idgame)) {
                let playerJson = sessionStorage.getItem(this.$route.params.idgame)
                let playerObj = JSON.parse(playerJson)
                this.namePalyer = playerObj["name"]


                var socket = io(window.location.origin);
                this.socketInstance = socket

                socket.on("newGameId", (id) => {
                    this.idPalyer = id
                    socket.emit("inPlayer", {name: this.namePalyer, idPalyer: id, idRoom: this.$route.params.idgame})            
                });

                socket.on("playersUpdate", (playersUpdate) => {
                    this.playersCards = playersUpdate["cardsOfRoom"]
                    this.isRevealed = playersUpdate["isRevealed"]
                    this.average = playersUpdate["average"]
                    this.votedCardsInfo = playersUpdate["votedCardsInfo"]

                    let votedCards = this.playersCards.filter(e=>e.step == "voted" || e.step == "revealed")
                    this.tatalVoted = votedCards.length
                    this.tatalPalyer = this.playersCards.length
                });

                socket.on("nonExistentRoom", (id_room) => {
                    this.$router.push(`/`)
                });
                
                
            } else {
                this.$router.push(`/${this.$route.params.idgame}`)
            }
        },

        methods: {

            onClickCardReveal() {
                this.socketInstance.emit("cardReveal", {idRoom: this.$route.params.idgame})
            },

            onClickRestartGame() {
                this.socketInstance.emit("restartGame", {idRoom: this.$route.params.idgame})
            },

            onClickChooseCard(voteValue, cardId) {
                Array.from(document.getElementsByClassName("choose-card-selected")).forEach(e=>{
                    e.classList.add("choose-card");
                    e.classList.remove("choose-card-selected");
                }) 
                document.getElementById(cardId).classList.remove("choose-card");
                document.getElementById(cardId).classList.add("choose-card-selected");

                this.socketInstance.emit("voting", {vote: voteValue, idPalyer: this.idPalyer, idRoom: this.$route.params.idgame})
            },
            
            getCardCssClassByStep(step) {

                let steps = {
                    no_voted: "card-not-selected",
                    voted: "card-selected",
                    revealed: "card-reveal"
                }

                return steps[step]
            }
        }
        
    }

</script>


